.show-block {
  -webkit-animation: showBlock 0.4s forwards;
          animation: showBlock 0.4s forwards;
  display: block !important;
}
@-webkit-keyframes showBlock {
from {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes showBlock {
from {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
.hide-block {
  display: block !important;
  -webkit-animation: hideBlock 0.4s forwards;
          animation: hideBlock 0.4s forwards;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
@-webkit-keyframes hideBlock {
from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    display: none;
}
}
@keyframes hideBlock {
from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    display: none;
}
}
